export function TextCT(props) {
  const { CT } = props
  return (
    <>
      {CT === 0 ? '-' : CT}
    </>
  )
}

export function TextAra(props) {
  const { Ara, AP } = props
  return (
    <>
      {AP === 'P' ? '-' :
        Ara === 0 ? '単体' :
          (Ara * 10) % 10 === 1 ? '周囲' + Math.floor(Ara) :
            (Ara * 10) % 10 === 2 ? '直線' + Math.floor(Ara) :
              Ara >= 10 ? '全体' : Ara
      }
    </>
  )
}

export function TextRng(props) {
  const { Rng, AP } = props
  return (
    <>
      {AP === 'P' ? '-' :
        Rng === 0 ? '自' : Rng}
    </>
  )
}

export function TextRv(props) {
  const { text } = props
  return (
    <span style={{ color: 'white', backgroundColor: 'lightgray', borderRadius: 2 }}>
      {text}
    </span>
  )
}


