const dataMM = {
  '単': [1, 1, 1, 1],
  '射程1-2_単': [1, 1, 1, 1],
  
  '射程1-2_範囲1': [3, 5, 5, 5],
  '射程1_3x2': [4, 6, 6, 6],
  '範囲1-2': [3, 4, 4, 4],
  周囲1: [2, 3, 3, 3],
  '周囲1-2': [4, 6, 8, 10],
  '直線1-2': [2, 2, 2, 2],
  '直線1-3': [2, 3, 3, 3],
  前方T5: [3, 5, 5, 5],
  前方3x3: [4, 9, 9, 9],
  前方3x4: [4, 9, 12, 12],
  '前方1+3x3': [4, 9, 10, 10],
  '3x2': [4, 6, 6, 6],
  '3マス先と両隣': [2, 3, 3, 3],
  '周囲2--範囲1': [4, 5, 7, 9],
  '射程1-2_3x1': [2, 3, 3, 3],
  '射程1_3x2': [4, 6, 6, 6],
  '射程1-3_十2': [3, 5, 7, 9],
}

export default function MM(Ara, Sz) {
  let Masu = 1
  if (Ara) {
    Masu = dataMM[Ara][Sz.split('x')[0] - 2]
  }
  return Masu
}