import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';

import Img from './Img';
import StyleCommon from './StyleCommon';



export default function DataGridDmgChr(props) {
  const { data,  Calc, list, cNameF, AE } = props.props

  const columns = [
    {
      field: 'No',
      headerName: '',
      width: 50,
      disableColumnMenu: true,//メニュー不可
      align: 'center',
    },

    {
      field: 'Nam',
      headerName: '',
      width: 150,
      disableColumnMenu: true,//メニュー不可

    },

    {
      field: 'Ara',
      headerName: '',
      headerAlign: 'center',
      width: 70,
      align: 'center',
      disableColumnMenu: true,//メニュー不可

      renderCell: (params) => (
        <Img name={params.row.Ara} width={70} />//height={35}
      )
    },
    {
      field: 'Dmg',
      headerName: 'ダメージ',
      headerAlign: 'center',
      align: 'right',
      width: 120,
      disableColumnMenu: true,//メニュー不可

      renderCell: (params) => params.row.DmgTxt
    },

    {
      field: 'DmgBar',
      headerName: '',
      width: 110,
      disableColumnMenu: true,//メニュー不可

      renderCell: (params) => (
        <>
          {AE === 'Ally' ?
            <Img name={'DmgBar'} width={params.row.Dmg / 100} height={100} /> :
            <>
              <Img name={'HPBar'} width={(params.row.ZHP - params.row.Dmg) / 20} height={100} />
              <Img name={'DmgBar'} width={params.row.Dmg / 20} height={100} />
            </>
          }
        </>
      )
    },



    {
      field: 'BasDmgStr',
      headerName: '基礎ダメージ&強化(+10)',
      width: 240,
      align: 'left',
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: 'Amp',
      headerName: '威力',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: '逆境盤石',
      headerName: '逆盤',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: '耐性',
      headerName: '耐性',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: '増減',
      headerName: '増減',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//メニュー不可

    },


  ];


  const rows = []
  data.C1[cNameF][AE].Skl2.forEach((SklEl, i) => {
    rows.push({
      ...SklEl,
      id: i,
      // No: SklEl.No,
      // Nam: SklEl.Nam,
      // Ara: SklEl.Ara,
      Elm: data.Skl[SklEl.Nam].Elm,
      // Dmg: SklEl.Dmg,
      // DmgTxt: SklEl.DmgTxt,
      DmgBar: SklEl.Dmg,
      ZHP: data.C1[cNameF].Ally.St.Btl.HP,
      // BasDmgStr: SklEl.BasDmgStr,
      // Amp: SklEl.Amp,
      // 逆境盤石: SklEl.逆境盤石,
    })


  })



  return (
    <Box sx={{ height: 250, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        // autoHeight//行に合わせた高さ
        hideFooter//フッター非表示
        hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）

        sx={StyleCommon}
        getRowClassName={(params) => params.row.Elm}
          

      // initialState={{
      //   pagination: {
      //     paginationModel: {
      //       pageSize: 5,
      //     },
      //   },
      // }}
      // pageSizeOptions={[5]}
      // checkboxSelection
      // disableRowSelectionOnClick
      // クラスを設定しておく

      />
    </Box>
  );
}