import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import Button from '@mui/material/Button';

import ModalOpSlct from "./ModalOpSlct"
import CNSplit from "./CNSplit"



const WeArBdToTxt = { Wepn: '武器', Armr: '防具', Bddy: 'Buddy' }



export default function DataGridEq(props) {
  const { data, C, cNameF, Calc, list, Op, Eq, handleClose, setEqs } = props.props

  let cName1 = CNSplit(cNameF,1)
  let cName2 = CNSplit(cNameF,2)


  const On = (event) => {

    // let OpBtest = Op[name][OpNum]
    // Op[name][OpNum] = event.target.name
    // setOp1s(event.target.name)
    // console.log(name + 'の特性' + OpNum + 'を' + OpBtest + 'から' + event.target.name + 'に変更')

    data.C1[cNameF].Ally[Eq] = event.target.name
    setEqs(event.target.name)

    Calc()
    handleClose()
  };




  const columns = [
    // { field: 'id', headerName: 'ID', width: 90 },
    // {
    //   field: 'Ctg',
    //   headerName: '分類',
    //   width: 50,
    // },
    {
      field: 'name',
      headerName: '名称',
      width: 200,
      renderCell: (params) => (
        <Button




          // id={i}
          name={params.row.name}
          // size="small"
          onClick={On}
        // name={params.row.name}
        // OpNum={1}
        // OpB={params.row.Op1}
        // data={data}
        // Op={Op}
        // Ctg={params.row.Eq}
        // Calc={Calc}
        >
          {params.row.name}
        </Button>
      )
    },
    {
      field: 'HP',
      headerName: 'HP',
      align: 'right',
      width: 50,
    },
    {
      field: 'TP',
      headerName: 'TP',
      align: 'right',
      width: 50,
    },
    {
      field: '攻撃力',
      headerName: '攻撃力',
      align: 'right',
      width: 50,
    },
    {
      field: '防御力',
      headerName: '防御力',
      align: 'right',
      width: 50,
    },
    {
      field: '速力',
      headerName: '速力',
      align: 'right',
      width: 50,
    },
    {
      field: '精神力',
      headerName: '精神力',
      align: 'right',
      width: 50,
    },
    {
      field: '移動力',
      headerName: '移動力',
      align: 'right',
      width: 50,
    },
    {
      field: 'UqOp',
      headerName: '固有特性',
      width: 200,
    },
    {
      field: 'Op1',
      headerName: '特性1',
      width: 200,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          OpNum={1}
          OpB={params.row.Op1}
          data={data}
          Op={Op}
          Ctg={params.row.Eq}
          Calc={Calc}
        />
      )

    },
    {
      field: 'Op2',
      headerName: '特性2',
      width: 200,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          OpNum={2}
          OpB={params.row.Op2}
          data={data}
          Op={Op}
          Ctg={params.row.Eq}
          Calc={Calc}
        />
      )
    },
    {
      field: 'Op3',
      headerName: '特性3',
      width: 200,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          OpNum={3}
          OpB={params.row.Op3}
          data={data}
          Op={Op}
          Ctg={params.row.Eq}
          Calc={Calc}
        />
      )

    },
    {
      field: 'Srs',
      headerName: 'シリーズ',
      width: 200,
    },
    {
      field: 'SrsEff',
      headerName: 'シリーズ効果',
      width: 400,
    },

  ];


  const rows = [];
  // Object.keys(WeArBdToTxt).forEach((Eq) => {
  Object.keys(data[Eq]).forEach((name, i) => {

    // console.log(data.wearable.アタッカー[data[Eq][name].Knd])
    // console.log(data.Chr[cName1][cName2].Typ)


    if (
      (Eq !== 'Armr') ||
      (Eq === 'Armr' && data.wearable[data.Chr[cName1][cName2].Typ][data[Eq][name].Knd])
      ) {

      let SrsEff = ''
      if (data[Eq][name].Srs) {
        SrsEff += '(1)' + data.Srs[data[Eq][name].Srs + '1'].SrsEffList
        if (data.Srs[data[Eq][name].Srs + '2']) {
          SrsEff += ',(2)' + data.Srs[data[Eq][name].Srs + '2'].SrsEffList
        }
      }
      rows.push({
        id: name,
        Eq: Eq,
        Ctg: WeArBdToTxt[Eq],
        name: name,
        HP: data[Eq][name].St.HP,
        TP: data[Eq][name].St.TP,
        攻撃力: data[Eq][name].St.攻撃力,
        防御力: data[Eq][name].St.防御力,
        速力: data[Eq][name].St.速力,
        精神力: data[Eq][name].St.精神力,
        移動力: data[Eq][name].St.移動力,
        UqOp: data[Eq][name].UqOp,
        Op1: Op[name][1],
        Op2: Op[name][2],
        Op3: Op[name][3],
        Srs: data[Eq][name].Srs,
        SrsEff: SrsEff,
      })

    }


  })
  // })




  return (
    <Box sx={{ height: 750, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        // autoHeight//行に合わせた高さ
        components={{
          Toolbar: GridToolbar,// ツールバーを指定する
        }}
        // hideFooter//フッター非表示
        // hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        // hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
        //   initialState={{
        //   pagination: {
        //     paginationModel: {
        //       pageSize: 10,
        //     },
        //   },
        // }}
        // pageSizeOptions={[5]}
      // checkboxSelection
      // disableRowSelectionOnClick
      />
    </Box>
  );
}