import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';

import ModalOpSlct from "./ModalOpSlct"


const WeArBdToTxt = { Wepn: '武器', Armr: '防具', Bddy: 'Buddy' }



export default function DataGridEq(props) {
  const { data, C, Calc, list, Op ,height} = props.props

  const columns = [
    // { field: 'id', headerName: 'ID', width: 90 },
    {
      field: 'Ctg',
      headerName: '分類',
      width: 50,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: 'name',
      headerName: '名称',
      width: 200,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: 'HP',
      headerName: 'HP',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: 'TP',
      headerName: 'TP',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: '攻撃力',
      headerName: '攻撃',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: '防御力',
      headerName: '防御',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: '速力',
      headerName: '速力',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: '精神力',
      headerName: '精神',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: '移動力',
      headerName: '移動',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: 'UqOp',
      headerName: '固有特性',
      width: 200,
    },
    {
      field: 'Op1',
      headerName: '特性1',
      width: 200,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          OpNum={1}
          OpB={params.row.Op1}
          data={data}
          Op={Op}
          Ctg={params.row.WAB}
          Calc={Calc}
        />
      )

    },
    {
      field: 'Op2',
      headerName: '特性2',
      width: 200,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          OpNum={2}
          OpB={params.row.Op2}
          data={data}
          Op={Op}
          Ctg={params.row.WAB}
          Calc={Calc}
        />
      )
    },
    {
      field: 'Op3',
      headerName: '特性3',
      width: 200,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          OpNum={3}
          OpB={params.row.Op3}
          data={data}
          Op={Op}
          Ctg={params.row.WAB}
          Calc={Calc}
        />
      )

    },
    {
      field: 'Srs',
      headerName: 'シリーズ',
      width: 600,
    },

  ];


  const rows = [];
  Object.keys(WeArBdToTxt).forEach((WAB) => {
    Object.keys(data[WAB]).forEach((name, i) => {

      let Srs = data[WAB][name].Srs
      if (data[WAB][name].Srs) {
        Srs += '(1)' + data.Srs[data[WAB][name].Srs + '1'].SrsEffList
        if (data.Srs[data[WAB][name].Srs + '2']) {
          Srs += ',(2)' + data.Srs[data[WAB][name].Srs + '2'].SrsEffList
        }
      }

      rows.push({
        id: name,
        WAB: WAB,
        Ctg: WeArBdToTxt[WAB],
        name: name,
        HP: data[WAB][name].St.HP,
        TP: data[WAB][name].St.TP,
        攻撃力: data[WAB][name].St.攻撃力,
        防御力: data[WAB][name].St.防御力,
        速力: data[WAB][name].St.速力,
        精神力: data[WAB][name].St.精神力,
        移動力: data[WAB][name].St.移動力,
        UqOp: data[WAB][name].UqOp,
        Op1: Op[name][1],
        Op2: Op[name][2],
        Op3: Op[name][3],
        Srs: Srs,



      })
    })
  })




  return (
    // <Box sx={{ height:height, width: '100%' }}>
    <DataGrid
      rows={rows}
      columns={columns}
      experimentalFeatures={{ newEditingApi: true }}
      density='compact'//行の幅
      autoHeight//行に合わせた高さ
      components={{
        Toolbar: GridToolbar,// ツールバーを指定する
      }}
      // hideFooter//フッター非表示
      // hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
      // hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
    //   initialState={{
    //   pagination: {
    //     paginationModel: {
    //       pageSize: 10,
    //     },
    //   },
    // }}
    pageSizeOptions={[5]}
    // checkboxSelection
    // disableRowSelectionOnClick
    getRowHeight={() => 'auto'}

    />
    // </Box>
  );
}