import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Modal from '@mui/material/Modal';
import Grid from '@mui/material/Unstable_Grid2';

import DataGridBddyOpSlct from "./DataGridBddyOpSlct"



const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 1000,
  bgcolor: '#fffef2',
  border: '2px solid #000',
  boxShadow: 24,
  p: 2,
};



export default function ModalOpSlct(props) {
  const { name, OpNum, OpB, data, Op, Ctg, Calc } = props


  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [Op1s, setOp1s] = React.useState(OpB);

  let OpList = ['テスト']
  if (data[Ctg][name].OpList) {
    OpList = data[Ctg][name].OpList
  }

  const On = (event) => {
    let OpBtest = Op[name][OpNum]
    Op[name][OpNum] = event.target.name
    setOp1s(event.target.name)
    console.log(name + 'の特性' + OpNum + 'を' + OpBtest + 'から' + event.target.name + 'に変更')
    Calc()

    handleClose()
  };


  return (
    <div>
      <Button onClick={handleOpen} size="small">{Op1s}</Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          {Ctg === 'Bddy' ?
            <DataGridBddyOpSlct
              props={{
                ...props,
                On: On,
                handleClose: handleClose
              }}



            /> :
            <Grid container spacing={0}>
              {OpList.map((OpName, i) => {
                return (
                  <Grid xs={4} key={i}>
                    <Button
                      id={i}
                      name={OpName}
                      size="small"
                      variant="outlined"
                      onClick={On}
                    >
                      {OpName}
                    </Button>
                  </Grid>
                )
              })}
            </Grid>

          }



        </Box>
      </Modal>
    </div>
  );
}